

.projectsContainer {
    margin-top: 10px;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}



.addProjectBtn{
    width: 115px;
    padding: 8px;
    border: 1px solid var(--light-gray-color);
    border-radius: 10px;
    box-shadow: 1px 1px 1px var(--light-gray-color-02);
    color: var(--gray-color);
    background-color: white;
    display: flex;
    gap: 5px;
}

.addProjectBtn:hover {
    cursor: pointer;
    border: 1px solid var(--main-active-color-02);
    color: var(--main-color-02);
}