.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* 배경색*/

body{
  background-color: #fafbfc;
}

/* 컬러 */
:root{
  --black-color: #242631;
--gray-color: #999ba7;
--gray-color-02: #797c8a;
--gray-color-03: #cccdd4;
--light-gray-color: #e3e3e8;
--light-gray-color-02: #f1f1f3;
--white-color: #fff;
--main-color: #4D4C7D;
--main-color-02: #827397;
--main-color-03: #363062;
--main-color-04 : #E9D5DA;
--main-bg-color: hsl(252, 77%, 97%);
--main-active-color: rgba(77, 76, 125, 0.8);
