

.relative {
  position: relative;
}

.flexCenterCol {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.flexStartCol {
  display: flex;
  justify-content: start;
  flex-direction: column;
}

.flexStartRow {
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
}

.flexCenterRow{
  display: flex;
  justify-content: center;
  flex-direction: row;
}

.alignCenter{
  align-items: center;
  gap: 2px;
}


.toolbar {
    padding: 5px;
    width: 40px;
    position: relative;
    background-color: var(--light-gray-color-02);
    border-radius: 0px 0px 0px 5px;
}



/* 컬러 프리뷰 구역 */

.PreviewColorSquare{
    width: 30px;
    height: 30px;
    border: 1px solid #555555;
}


.mainColorDiv {
    position: relative;
    z-index: 3;
}

.subColorDiv {
    position: relative;
    bottom: 20px;
    left: 8px;
    z-index: 1;
}

.PreviewColors{
    position: relative;
    margin-top: 3px;
    height: 45px;
    width : 40px;
}

.changeMainSubColorBtn{
    position:absolute;
    top: -4px;
    z-index:3;
    left: 32px;
    color : var(--gray-color);
}

.changeMainSubColorBtn:hover{
    color: var(--main-color);
    cursor: pointer;
}

.changeMainSubColorIcon{
    width: 13px;
    height: 13px;
}


.colorPickerPositionLeft {
    
    position: absolute; 
    left: 55px; 
    top: 115px;
    z-index: 5;
}