

.relative {
position: relative;
}

.flexCenterCol {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.flexStartCol {
    display: flex;
    justify-content: start;
    flex-direction: column;
}

.flexStartRow {
    display: flex;
    justify-content: flex-start;
    flex-direction: row;
}


.flexCenterRow {
    display: flex;
    justify-content: center;
    flex-direction: row;
}

.alignCenter {
    align-items: center;
    gap:2px;
}

.nonActiveToolBtn {
    background-color: rgba(0, 0, 0, 0);
    
    color: var(--gray-color-02);
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 5px;
    padding: 9px;
}
.nonActiveToolBtn:hover {
    background-color: rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 5px;
    padding: 9px;
}

.activeToolBtn {
    background-color: rgba(123, 104, 196, 0.1);
    color: var(--main-color);
    border: 1px solid rgba(77, 76, 125, 0.3);
    border-radius: 5px;
    padding: 9px;
}



.brushSizeDiv {
    position: absolute;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    left: 55px;
    z-index: 2;
    background-color: var(--white-color);
    border-radius: 10px;
    border: 1px solid var(--gray-color-03);
    color: var(--gray-color-02);
    padding: 4px;
    box-shadow: 2px 2px 5px #aaaaaa;
  }

.brushSizeDiv-free{
    top: 209px;
}

.brushSizeDiv-poly{
    top: 280px;
}

.brushSizeDiv-curve{
    top: 317px;
}

.closebrushSizeBtn{
    color: var(--black-color);
    background-color: rgba(255, 255, 255, 0);
    border: none;
}

.closebrushSizeBtn:hover {
    cursor: pointer;
    color: var(--gray-color);
}



