/*폰트설정*/


.fontSettingsBox {

    position: absolute;
    /* left: 50px;
    top: 580px; */

    width: 300px;
    height: 50px;
    z-index: 3;
    background-color: var(--white-color);
    border-radius: 10px;
    border: 1px solid var(--gray-color-02);
    padding: 5px;
    box-shadow: 2px 2px 5px #aaaaaa;
  }


  .fontFamilySelbox{
    width: 170px;
    height: 25px;
  }

  .fontsize{
    width: 50px;
    height: 19px;
  }

  .fontIcon{
    width: 18px;
    height: 18px;
  }

.nonActiveSettingBtn {
    width: 30px;
    height: 30px;
    background-color: rgba(0, 0, 0, 0);
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 2px;
}
.nonActiveSettingBtn:hover {
    width: 30px;
    height: 30px;
    background-color: rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 2px;
}

.activeSettingBtn {
    width: 30px;
    height: 30px;
    /* background-color: rgba(68, 0, 194, 0.1); */
    color: var(--main-color);
    background-color: rgba(0, 0, 0, 0);
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 2px;
}

.dividerIcon{
    width: 25px;
    height: 25px;
    color: var(--gray-color);
}



