

.projectItemContainer{
    width: 300px;
    height: 210px;
    border: 1px solid var(--gray-color-03);
    border-radius: 5px;
    box-shadow: 2px 2px 10px var(--light-gray-color);
}
.projectItemContainer:hover{
    border: 1px solid var(--main-active-color-02);
}

.projectItemPreview{
    width: 298px;
    height: 160px;
    border: 1px solid var(--light-gray-color);
    border-radius: 5px 5px 0px 0px;
}
.projectItemPreview:hover{
    cursor: pointer;
}

.projectContent{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 7px 10px;
}

.projectDelBtn{
    color: var(--gray-color-02);
    background-color: rgba(255, 255, 255, 0);
    border: none;
    margin-right: 3px;
    padding-bottom: 2px;
}
.projectDelBtn:hover {
    cursor: pointer;
    color: var(--gray-color);
}


.projectChangeNameBtn{
    color: var(--gray-color);
    background-color: rgba(255, 255, 255, 0);
    border: none;
    margin-left: 5px;
}

.projectChangeNameBtn:hover {
    cursor: pointer;
    color: var(--gray-color-03);
}
